.container {
  padding: 64px 0;
  display: flex;
  flex-direction: column;
  gap: 40px;
  width: 100%;

  .itemsWrapper {
    width: 100vw;
    display: flex;
    justify-content: center;
  }

  .items {
    display: flex;
    gap: 21px;
    overflow-x: auto;
    padding: 21px;
    & > * {
      width: 100%;
      flex: none;
    }
  }

  .button {
    background-color: var(--blue);
    border: none;
    border-radius: 16px;
    height: 64px;
    width: 100%;
    max-width: 356px;
    margin-left: auto;
    margin-right: auto;
  }
}
